@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
*,*::after,*::before{
  margin: 0;
}


body{
  font-family: 'Roboto', sans-serif;
}

::-webkit-scrollbar{
  display: none;
}

.label,
.label-range{
    margin-bottom: 0.8rem;
    font-weight: 600;

}

.input-group{
    margin-bottom: 2rem;
}
.label-range{
    margin-bottom: 2.5rem;
}
.searchBar-wrap{
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgb(49, 46, 46);
    padding: 1.5rem 1rem;
}

.searchBar-icon{
    color: rgb(68, 66, 66);
    margin-right: 2rem;
}

.searchBar-wrap input{
    font-size: 1.5rem;
    outline: none;
    border: none;
    width: 100%;
}

.searchBar-wrap input::-webkit-input-placeholder{
    color: rgb(68, 66, 66);
}

.searchBar-wrap input:-ms-input-placeholder{
    color: rgb(68, 66, 66);
}

.searchBar-wrap input::placeholder{
    color: rgb(68, 66, 66);
}
.listItem-wrap img{
    width: 100%;
    border-radius: 15px;
    height: 200px;
    object-fit: cover;
}

.listItem-wrap header{
    display: flex;
    justify-content: space-between;
    margin: 1rem 0;
    align-items: center;

}

.listItem-wrap header span{
    font-size: .7rem;
    background-color: #97ffff;
    padding: .5rem;
    border-radius: 10px;
}

.listItem-wrap footer{
    font-size: .8rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

}
.listItem-wrap footer p:first-child span{
    color: rgb(92, 89, 226);
    font-weight: 600;
}


.list-wrap{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 2rem;
}
.home{
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.home_panelList-wrap,
.home_panel-wrap,
.home_list-wrap{
    overflow-y: auto;
}

.home_panelList-wrap{
    display: flex;
    flex: 1 1;
}

.home_panel-wrap{
    flex-basis: 280px;
    padding: 1rem;
}
.home_list-wrap{
    flex: 1 1;
    padding: 1rem;
}

.emptyView-wrap{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.emptyView-wrap img{
    max-width: 500px;
    width: 100%;
}
